
.body{
    background: #f2f2f2;
    font-family: Arial, sans-serif;
    display: flex;
    justify-content:center;
    align-items:center;
    height: 100vh;
    margin: 0;
    background-color: rgb(230, 231, 239);
    background-image: url(https://th.bing.com/th/id/OIP.DbpphLwl4aS-9ojlOFo-qwHaEK?w=260&h=180&c=7&r=0&o=5&dpr=1.3&pid=1.7);
    background-repeat: no-repeat;
    background-size: cover;
}

.login-container {
    background: #050505;
    opacity: 0.89;
    border-radius: 15px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    width: 30vw;
    height: auto;
}
.login-header {
  
    color: #fff;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;

}
.login-header h2 {
    font-size: 40px;
}
.login-form {
    padding: 3vh 5vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.form-group {
    margin-bottom: 20px;
    color: #050505;
    display: flex;
    align-items: center;
    justify-content: center;
}

.form-group label {
    display: block;
    font-weight: bold;
}

.form-group input {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
    background-color: #050505;
}
a{
    text-decoration: none;
    color: white;
}
.or{
    width: 100%;
    display: flex;
    color: white;
    justify-content: center;
    align-items: center;
}
.button-group{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.signin-button {
    background: #e43030;
    color: #ffffff;
    border: none;
    border-radius: 3px;
    padding: 10px 20px;
    font-size: 18px;
    cursor: pointer;
    align-self: center;
    transition: background 0.3s ease;
    width: 22vw;
}
.sign-in-code{
    color: #fff;
    background-color: #999999;
    border: none;
    border-radius: 3px;
    padding: 10px 20px;
    font-size: 18px;
    cursor: pointer;
    transition: background 0.3s ease;
    width: 22vw;
}

.signin-button:hover {
    background: #ff7b7b;
}
.sign-in-code:hover{
    background: #c2c2c2;
}
a:hover{
    color: #999999;
}
.forgot-password_and_sign_up {
    margin-top: 25px;
    width: 22vw;
    display: flex;
    text-decoration:none;
    color: #e43030;
    justify-content:space-around
}

#username , #password{
    width: 20vw;
}
@media only screen and (max-width: 1000px){
    .login-container {
        width: 35vw;
    }
    .signin-button {
        width: 26vw;
    }
    .sign-in-code{
        width: 26vw;
    }
    .forgot-password_and_sign_up {
        width: 28vw;
    }
    .login-form {
        padding: 3vh 5vw;
    }
    #username , #password{
        width: 25vw;
    }
}
@media only screen and (max-width: 750px){
    .login-container {
        width: 45vw;
    }
    .signin-button {
        width: 35vw;
    }
    .sign-in-code{
        width: 35vw;
    }
    .forgot-password_and_sign_up {
        width: 36vw;
    }
    .login-form {
        padding: 3vh 5vw;
    }
    #username , #password{
        width: 35vw;
    }
    .login-header h2 {
        font-size: 32px;
    }
}
@media only screen and (max-width: 600px){
    .login-container {
        width: 55vw;
    }
    .signin-button {
        width: 45vw;
    }
    .sign-in-code{
        width: 45vw;
    }
    .forgot-password_and_sign_up {
        width: 46vw;
    }
    .login-form {
        padding: 3vh 5vw;
    }
    #username , #password{
        width: 45vw;
    }
    .login-header h2 {
        font-size: 30px;
    }
}
@media only screen and (max-width: 450px){
    .login-container {
        width: 70vw;
    }
    .signin-button {
        width: 55vw;
    }
    .sign-in-code{
        width: 55vw;
    }
    .forgot-password_and_sign_up {
        width: 56vw;
    }
    .login-form {
        padding: 3vh 7vw;
    }
    #username , #password{
        width: 55vw;
    }
    .login-header h2 {
        font-size: 30px;
    }
}